<template>
    <div :ref="name"></div>
</template>

<script>
import 'flatpickr/dist/flatpickr.css';
import flatpickr from 'flatpickr';

export default {
    props: {
        name: String,
        config: Object,
        /**
         * returns
         *
         * selectedDates - an array of Date objects selected by user
         * dateStr - a string representation of the latest selected Date object by the user
         * instance - the flatpickr object, containing various methods and properties
         */
        onChange: Function
    },
    mounted() {
        const instance = flatpickr(this.$refs[this.name], this.config);

        instance.config.onChange.push(this.onChange)
    }
}
</script>