<template>
     <div class="MainBooking__ScheduleTime">
        <div class="CntSS" v-if="CoachSelected.length !== 0">
            <div class="CntSS__Left">
                <div class="CntSS__Left--Top">
                    <div class="CltIcon">
                        <cloud-rain-icon size="2.5x" class="custom-class"></cloud-rain-icon>
                    </div>
                    <div class="CltTitle">
                        <h1>Schedule a lessons</h1>
                        <span>Neo live: Reactions and Ratios</span>
                    </div>
                    
                </div>
                <div class="CntSS__Left--Bottom">
                    <calendar  
                        name="booking" 
                        :onChange =  onChange
                        :config="{
                            inline: true,
                            minDate: mindate,
                            
                        }"
                      
                    ></calendar>
                </div>


            </div>
            <div class="CntSS__Center">
                <div class="CntSS__Center--Top">
                    Pick a time
                </div>
                <div class="CntSS__Center--Bottom">
                    
                    <div class="BtTimePick" id="style-3" v-if="TimeStart == null || TimeStart.length == 0">
                        <div class="BtTimePick__NotReady">
                            <info-icon size="1.5x"></info-icon>
                            <h5>No Available Coach Schedule</h5>
                        </div>
                    </div>
                    <div class="BtTimePick" id="style-3" v-else>
                        <span 
                            v-for="(n, index) in TimeStart" :key="index"
                            :class="{IsActive:index == timeIndex}"
                            @click="timeSelected(n)">{{n}}</span
                        >
                        <span style=" visibility: hidden;"></span>
                        <span style=" visibility: hidden;"></span>
                        <span style=" visibility: hidden;"></span>
                        
                    </div>
                </div>
            </div>
            <div class="CntSS__Right" v-for="(n, i) in CoachSelected" :key="i">
                <div class="CntSS__Right--Top">
                    <div class="profile">
                        <div 
                            class="profile__img" 
                            v-bind:style="{ 'background-image': 'url(' + n.picture.original + ')' }"
                            >
                        </div>
                        <div class="profile__bio">
                            
                            <div class="profile__bio--name">
                                {{n.name}}
                            </div>
                            <div class="profile__bio--AddInfo">
                               
                                <div class="info">
                                    <div class="BioIcon"> 
                                       <tag-icon size=".7x"></tag-icon>
                                    </div>
                                    <span>{{n.fee}} Tokens</span>
                                </div>
                                 <div class="info">
                                    <div class="BioIcon"> 
                                        <map-pin-icon size=".7x"></map-pin-icon>
                                    </div>
                                     <span>London, UK</span>
                                </div>
                                <div class="info">
                                    <div class="BioIcon"> 
                                        <volume-2-icon size=".7x"></volume-2-icon>
                                    </div>
                                     <span>London, UK</span>
                                </div>
                            </div>
                        </div>
    

                    </div>
                </div>
                
                <div class="CntSS__Right--Bottom">
                    <div class="addTime">
                        <div class="addTime__date">
                            <span class="addTime__date--icon">
                                <calendar-icon size="1.2x"></calendar-icon>
                            </span>
                            
                            <div class="addTime__date--detail">
                                <div>{{date}}</div>
                                <span>Date</span>
                            </div>
                        </div>
                        <div class="addTime__date">
                            <span class="addTime__date--icon">
                                <clock-icon size="1.2x"></clock-icon>
                            </span>
                            <div class="addTime__date--detail">
                                <div>{{time}}</div>
                                <span>Time</span>
                            </div>
                        </div>
                        <div class="addTime__date">
                            <span class="addTime__date--icon">
                                <crosshair-icon size="1.2x" class="custom-class"></crosshair-icon>
                            </span>
                            <div class="addTime__date--detail">
                                <div>25 min</div>
                                <span>Duration</span>
                            </div>
                        </div>
                        <div class="addTime__btn">
                            <button :disabled="!time"  :class="{disabled:!time}" class="btnNeo"  @click="bookcoach">Book coach </button>
                        </div>
                    </div>
                    <div class="Backgrounds">
                         <RocketIcon />
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import { mapState } from 'vuex';
    import 'flatpickr/dist/flatpickr.css';
    import Calendar from '@/components/utilities/Calendar';
    import RocketIcon from '@/components/icons/RocketIcon.vue';
    import { CloudRainIcon,  MapPinIcon, Volume2Icon, CalendarIcon, ClockIcon, CrosshairIcon, InfoIcon, TagIcon} from 'vue-feather-icons'; 
    export default {
        components: {
            CloudRainIcon,
            MapPinIcon,
            Volume2Icon,
            CalendarIcon,
            ClockIcon,
            CrosshairIcon,
            Calendar,
            InfoIcon,
            RocketIcon,
            TagIcon
        },
        data() {
            return {
                date: null,
                mindate:new Date(Date.now() + 1 * 24*3600*1000).toISOString().substr(0, 10),
                time:null,
                timeIndex:null,
                coachID:null
            }
        },
        created(){
            this.coachID = this.$route.params.id
            if(this.idCoach !== this.$route.params.id ){
                this.$router.push('/student/book')
            }

            console.log(this.CoachSelected)
        },
        mounted() {
           
        },
        methods: {
            onChange(selectedDates, dateStr) {
                this.date = dateStr;
                this.timeIndex = null
                this.time = null
                this.$store.dispatch('booking/getTimeSchedule', dateStr);
                this.$router.push(
                    { 
                        query: Object.assign({}, this.$route.query, { date:this.date || undefined })
                    }
                ).catch(err => {
                    console.log(err)
                })
            },
            bookcoach(){
                console.log('click')
            },
            timeSelected(n){
                this.timeIndex = this.TimeStart.indexOf(n)
                this.time = n
                this.routeRepl()
            },
            routeRepl(){
                this.$router.push(
                    { 
                        query: Object.assign({}, this.$route.query, {  time:this.time || undefined }) 
                    }
                ).catch(err => {
                    console.log(err)
                })
            }
        },  
        computed: {
            ...mapState({
                coachInfo: state => state.booking.allcoach,
                idCoach : state => state.booking.coachID,
                timeSchdl: state => state.booking.timeSchedule
            }),

            CoachSelected(){
                const ID = this.coachID;
                const bankCoach = this.coachInfo.data 
                if(bankCoach !== undefined){
                    const selectedCH = bankCoach.filter(function(x){
                        return x.id == ID;
                    })
                    return selectedCH
                }else{
                    return ''
                }
            },

            TimeStart(){
                const SchTime = this.timeSchdl 
                if(SchTime){
                    const startTime = SchTime.slot.times.map(x => x.start);

                    return startTime
                }else{
                    return ''
                }
            }

            
        }
    };
</script>